import React from "react"
import theme from "../gatsby-plugin-theme-ui"
import { StyledInitiatives, StyledInitiativesContainer } from "./styles"
import InitiativeItem from "./InitiativeItem"
import { css } from "@emotion/core"

const Initiatives = () => {
  return (
    <StyledInitiatives>
      <div>
        <h1> Intiatives </h1>
        AIM is a community of undergraduates aimed at promoting and fostering the growing interest around machine intelligence on campus. We hold weekly discussions on the latest papers in the field, organize workshops, host speakers, and arrange competitions around machine intelligence at MIT.
      </div>

      <StyledInitiativesContainer>
        <InitiativeItem>Reading Group</InitiativeItem>
        <InitiativeItem>Workshops</InitiativeItem>
        <InitiativeItem>Generator</InitiativeItem>
        <InitiativeItem>Labs</InitiativeItem>
      </StyledInitiativesContainer>

    </StyledInitiatives>
  )
}

export default Initiatives
